<template>
    <div class="interval" :class="{limitflex:limittype==1}">
        <div class="title" :class="{limit:limittype==1}">{{title}}</div>
        <div class="time">
            <span>{{day | numLen}}</span>:
            <span>{{hour | numLen}}</span>:
            <span>{{minute | numLen}}</span>:
            <span>{{second | numLen}}</span>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                times: '0',
                day: '00',
                hour: '00',
                minute: '00',
                second: '00',
                title: ''
            }
        },
        props: {
            start: {
                type: Number,
                default(){
                    return 0
                }
            },
            end: {
                type: Number,
                default(){
                    return 0
                }
            },
             limittype:{
                type: Number,
                default(){
                    return 0
                }
            }
        },
        methods: {
            getTime() {
                // 获取当前时间
                return new Promise((resolve, reject) => {
                    this.times = (new Date().getTime()/1000).toFixed(0)
                    // console.log('this.times')
                    // console.log(this.times)
//                    this.times = t
                    resolve(this.times)
                })
            },
            compare(s) {
                // 比较当前时间与开始或结束时间  返回t时间
                return new Promise((resolve, reject) => {
                    const now = s
                    const start = this.start
                    const end = this.end
                    const limittype = this.limittype
                    let t = 0
                    if (now - start < 0) {
//                      console.log('未开始') 
                        if(limittype === 1){
                            this.title = '距离限时购开始'
                        }else{
                            this.title = '距离开始'
                        }
                        t = start - now
//                      console.log('倒计时时间')
//                      console.log(t)
                        resolve(t)
                    } else if (now - start > 0 && now < end) {
//                      console.log('进行中')
                        if(limittype === 1){
                            this.title = '距离限时购结束'
                        }else{
                            this.title = '距离结束'
                        }
                        t = new Date(end - now)
                        console.log(t)
                        resolve(t)
                    } else {
//                      console.log('结束')
                        if(limittype === 1){
                            this.title = '活动已结束'
                        }else{
                            this.title = '已结束'
                        }
                        reject('结束了')
                    }
                })

            },
            action(s) {
                // t倒计时
//              console.log('参数s')
//              console.log(s)
                return new Promise((resolve, reject) => {
                    if (s < 1) {
                        resolve()
                    } else {
//                      console.log('s>1000')
//                      console.log(s)
                        let t = s
                        let id = setInterval(() => {
                            t = t - 1
                            this.setTimeCount(t)
                            if (t < 0) {
//                              console.log('t小于0结束')
                                clearInterval(id)
                                resolve()
                            }
                        }, 1000)
                    }
                })
            },
            setTimeCount(t) {
                const d = t % 86400
                let day = t >= 86400 ? (t-d) / 86400 : 0
                const h = d % 3600
                let hour = d >= 3600 ? (d - h) / 3600 : 0
                const m = h % 60
                let min = h >= 60 ? (h - m) / 60 : 0
                const s = m % 1
                let second = m >= 1 ? (m - s) / 1 : 0
                this.day = day
                this.hour = hour
                this.minute = min
                this.second = second
            },
            init() {
//              console.log(this.start)
//              console.log(this.end)
                this.getTime()
                    .then(this.compare)
                    .then(this.action)
                    .then(() => {
                    this.init()
                }).catch((error) => {
//                  console.log('catch')
                    this.day = '00'
                    this.hour = '00'
                    this.minute = '00'
                    this.second = '00'
                })
            }
        },
        watch:{
            start(v){
                this.init()
            }
        },
        filters: {
            numLen(value) {
                const len = 2
                let valueLen = String(value).length
                if (valueLen <= len) {
                    return `${'0'.repeat(len - valueLen) + value}`
                } else if (valueLen > len) {
                    return `${value}`
                }
            }
        },
        mounted(){
            this.init()
        }
    }
</script>
<style scoped>
    .interval {
        width: 1rem;
    }
    .title {
        color: #000;
        font-size: .12rem;
        margin:0 .02rem .02rem
    }
    .time {
        display: flex;
        justify-content: center;
        color: #999;
    }
    .time span {
        display: block;
        color: #fff;
        min-width: .2rem;
        height: .2rem;
        background: rgba(51, 51, 51, 0.71);
        font-size: .1rem;
        line-height: .2rem;
        border-radius: .03rem;
        margin: 0 .01rem;
    }
    .limit{
        color: #fff;
        background: #ef4f4f;
        padding: 0.02rem 0.1rem;
        margin-right: 0.02rem;
        border-radius: 0.04rem; 
    }
    .limitflex{
        display: flex;
        width: auto;
    }
</style>



